import {
  createStore
} from 'vuex'
import Gpuzzle from './modules/Gpuzzle'
import GmoneyBills from './modules/GmoneyBills'
import GgridStory from './modules/GgridStory'
import Gmori from './modules/Gmori'
import GfindInImg from './modules/GfindInImg'
import GarrangeSentences from './modules/GarrangeSentences'

export default createStore({
  state: {
    name: "",
    endEscapeRoom: false,
    endTime: {},
    ifMuted: false,
    rooms: [{
        text: " 1",
        link: "/MainRoom/1",
        code: 14,
        ifCheck: false,
        ifResolved: false,
        id: 1
      },
      {
        text: " 2",
        link: "/MainRoom/2",
        code: 15,
        ifCheck: false,
        ifResolved: false,
        id: 2
      },
      {
        text: " 3",
        link: "/MainRoom/3",
        code: 7,
        ifCheck: false,
        ifResolved: false,
        id: 3
      },
      {
        text: " 4",
        link: "/MainRoom/4",
        code: 7,
        ifCheck: false,
        ifResolved: false,
        id: 4
      },
      {
        text: " 5",
        link: "/MainRoom/5",
        code: 4,
        ifCheck: false,
        ifResolved: false,
        id: 5
      },
      {
        text: " 6",
        link: "/MainRoom/6",
        code: 20,
        ifCheck: false,
        ifResolved: false,
        id: 6
      }
    ],
    codesCorrectOrder: [{
      correct: 7,
      input: ""
    },
    {
      correct: 14,
      input: ""
    },
    {
      correct: 15,
      input: ""
    },
    {
      correct: 20,
      input: ""
    },
    {
      correct: 7,
      input: ""
    },
    {
      correct: 4,
      input: ""
    }
  ]
  },
  mutations: {
    pushName(state, name) {
      state.name = name;
    },
    endTask(state, id) {
      state.rooms.forEach((el) => {
        if (el.id === id) {
          el.ifCheck = true;
        }
      })
    },
    completeRoom(state, id) {
      state.rooms.forEach((el) => {
        if (el.id === id) {
          el.ifResolved = true;
        }
      })
    },
    endEscapeRoom(state) {
      state.endEscapeRoom = true;
    },
    passEndTimer(state, secondsAndMinutes) {
      state.endTime = secondsAndMinutes;
    },
  },
  actions: {},
  modules: {
    Gpuzzle,
    GmoneyBills,
    GgridStory,
    Gmori,
    GfindInImg,
    GarrangeSentences
  }
})